@import url(https://fonts.googleapis.com/css2?family=Inter&display=swap);
.spinner {
	width: 40px;
	margin: 21px auto;
}

.movie {
  display: flex;
	height: 281px;
	overflow: hidden;
	box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.15);
}

.movie-info {
  font-weight: 400;
  font-style: normal;
  font-family: 'Inter', sans-serif;
}

.movie img {
  margin-right: 20px;
}

.movie-info h2 {
  color: #000;
  font-size: 20px;
  line-height: 28px;
  margin: 12px 0 7px;
}

.movie-releaseDate {
  color: #827e7e;
  font-size: 12px;
  line-height: 22px;
  margin-bottom: 7px;
}

.movie-description {
	color: #000;
	font-size: 12px;
	line-height: 22px;
	margin: 7px 20px 49px 0;
}

ul {
	margin: 0;
	padding: 0;
  list-style-type: none;
}

.movieGenres {
	margin: 0;
	display: flex;
}

.movieGenres-genre {
	padding: 3px;
  font-size: 12px;
	margin-right: 8px;
  line-height: 14.52px;
  background-color: #fafafa;
  border: 1px solid #d9d9d9;
	color: rgba(0, 0, 0, 0.65);
}

.main {
	margin-top: 21px;
}

.movieApp {
  width: 1010px;
  margin: 0 auto;
}

